/* Css for About  */

.content__section {
    width: 100%;
    height: auto;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 1rem 2rem 1rem 6rem;
}

.content__container {
    width: 100%;
    margin: 0 auto;
    height: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
    background: var(--secondary-color);
    padding: 1rem;
    border-radius: 10px;
}

.content__container h2 {
    color: white;
    font-size: 4rem;
    margin: .5rem 0rem;
}

.portfolio__container {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}
