/* importing fonts  */
@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap");

/* Css For Sidebar Component */
.sidebar__section {
    width: 100px;
    max-width: 100px;
    overflow-x: hidden;
    overflow-y: auto;
    height: 100vh;
    background: var(--primary-color);
    opacity: 0.7;
    position: fixed;
}

.brand__container {
    width: 100%;
    padding: 1rem;
    border-bottom: 1px dashed var(--border-color);
}

.brand__name {
    font-size: 2rem;
    color: var(--text-color);
    font-family: "Great Vibes", cursive;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.navigation__container {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 4rem;
    padding: 3rem 1rem;
}

.nav__item {
    color: var(--text-color);
    font-size: 2rem;
}