/* Css for About  */

.content__section {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content__container {
    width: 90%;
    margin: 0 auto;
    height: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    background: var(--secondary-color);
    padding: 1rem;
    border-radius: 10px;
}

h2 {
    color: white;
    font-size: 2rem;
}
