/* initiliznig fonts  */

:root {
    --primary-color: #000;
    --secondary-color: rgba(0, 0, 0, 0.5);
    --text-color: #f6d365;
    --heading-font-family: "Recursive", sans-serif;
    --text-font-family: "Cedarville Cursive", cursive;
    --white-color: #fff;
    --border-color: #f6d365;
}

*,
*::after,
*,
*::before {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style-type: none;
    box-sizing: border-box;
    font-size: 16px;
}

body {
    background: #000 url(/portfolio-react/static/media/bg.68c59def.jpg) top left no-repeat;
    background-size: cover;
    width: 100vw;
    height: auto;
    overflow-y: auto;
    overflow-x: hidden;
    background-attachment: fixed;
}

/* Typewriter effect css  */
.Typewriter__wrapper {
    font-size: 3rem;
    color: #fff;
    color: var(--white-color);
    font-weight: bold;
    font-family: "Great Vibes", cursive;
}

