/* Style Sheet form Card  */

.Card_card__container__3-VCv {
    width: 32%;
    height: 600px;
    max-height: 600px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
}

.Card_card__container__3-VCv::after {
    content: attr(data-title);
    position: absolute;
    top: 0%;
    left: 0;
    width: 0%;
    height: 600px;
    background: rgba(0, 0, 0, 0.7);
    color: var(--white-color);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 2rem;
    overflow: hidden;
    transition: all 1s cubic-bezier(0, 1.49, 1, 0.01);
    opacity: 0;
}

