/* Css for Content  */

.content__section {
    width: 100%;
    height: auto;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 1rem 2rem 1rem 6rem;
}

.content__container {
    width: 100%;
    margin: 0 auto;
    height: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    background: var(--secondary-color);
    padding: 1rem;
    border-radius: 10px;
}

.profile {
    width: 50%;
    padding: 1rem;
}

.profile img {
    width: 100%;
    border-radius: 100%;
}

.profile__descrption {
    width: 50%;
    padding: 1rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.profile__descrption h2 {
    font-size: 2rem;
    color: var(--white-color);
    font-family: var(--heading-font-family);
}