/* Style Sheet form Card  */

.card__container {
    width: 32%;
    height: 600px;
    max-height: 600px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
}

.card__container::after {
    content: attr(data-title);
    position: absolute;
    top: 0%;
    left: 0;
    width: 0%;
    height: 600px;
    background: rgba(0, 0, 0, 0.7);
    color: var(--white-color);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    overflow: hidden;
    transition: all 1s cubic-bezier(0, 1.49, 1, 0.01);
    opacity: 0;
}

.card__container:hover::after {
    opacity: 1;
    width: 100%;
}

.card__img__container {
    width: 100%;
}

.card__img__container img {
    width: 100%;
}
