/* Design Cards */

.servce__card {
    width: 30% !important;
    height: max-content;
    padding: 2rem;
    box-sizing: border-box;
    border: 1px solid var(--border-color);
    background: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
}

.servce__card:hover{
    background: rgba(0, 0, 0, 1);
    transform: scale(1.1);
}


.service__body {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}

.services__heading {
    width: 100%;
}

.services__heading h3 {
    font-size: 1.5rem;
    color: var(--white-color);
    text-align: center;
}

.service__content {
    width: 100%;
    padding: 0.5rem;
}

.service__content p {
    font-size: 1.1rem;
    color: var(--white-color);
}

.service__image {
    width: 100%;
    text-align: center;
}

.service__image svg {
    color: white;
    font-size: 4rem;
}

/* Design Cards Ends */
